export class Tag {
  constructor(id, content) {
    this.id = id;
    this.content = content;
    this.id = id;
    this.content = content;
  }
  get info() {
    const info = `id:${this.id},content:${this.content}`;
    console.log(`log:${info}`);
    return info;
  }
  updateContent(tagContent) {
    this.content = tagContent;
    return true;
  }
}
